                      <p v-if="item.orderStatus!='8'"><span>价格：<i>{{item.orderPrice}}万</i></span><span :style="(item.orderStatus=='1'||item.orderStatus=='3')?'color:#FF3F12':''">{{item.orderStatusStr}}</span></p>
                    </div>
                  </li>
                  <!-- <li>
                    <button class="btn" v-if="item.orderStatus==1?true:false">继续付款</button>
                    <button class="btn" @click="toApplyRefund(item.id)" v-if="item.orderStatus==2||item.orderStatus==6||item.orderStatus==8?true:false">申请退款</button>
                    <button class="btn" v-if="item.orderStatus==10?true:false" @click="friend">好友推荐</button>
                    <router-link tag="button" :to="{name:'orderinfo',params:{info:item.id}}" class="btn">查看详情</router-link>
                  </li> -->
                  <li v-if="item.orderStatus==14">
                      <button class="btn" @click="toApplyRefund(item.id)">退款明细</button>
                      <button class="btn" @click="withdraw(item)">撤销申请</button>
                  </li>
                   <li v-if="item.orderStatus==15">
                     <button class="btn" @click="orderinfo(item.id)">查看详情</button>

                  </li>
                   <li v-if="item.orderStatus==12">
                     <button class="btn" @click="orderinfo(item.id)">查看详情</button>
                  </li>
                </div>
              </div>
              <div v-if="order6List.length==0" class="nullTip">
                <img src="../../assets/image/tipNull.png">
                <p>暂无数据</p>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="已完成">
          <div class="orderlist-date">
            <li>选择日期</li>
            <li @click="popupBegin">
              <span class="text-time">{{yearBegin}}{{monthBegin}}{{dateBegin}}</span>
              <van-popup v-model="showBegin" position="bottom" :overlay="false">
                <van-datetime-picker
                  v-model="currentDateBegin"
                  type="date"
                  @confirm="selectedBegin(currentDateBegin)"
                  :formatter="formatter"
                />
              </van-popup>
              <span></span> <span></span><img src="../../assets/image/date.png" alt="">
            </li>
            <li><p></p></li>
            <li @click="popupEnd">
              <span class="text-time">{{yearEnd}}{{monthEnd}}{{dateEnd}}</span>
              <van-popup v-model="showEnd" position="bottom" :overlay="false">
                <van-datetime-picker
                  v-model="currentDateEnd"
                  type="date"
                  @confirm="selectedEnd(currentDateEnd)"
                  :min-date="minDateEnd"
                  :formatter="formatter"
                />
              </van-popup>
              <span></span> <span></span><img src="../../assets/image/date.png" alt="">
            </li>
            <li @click="toSearch(selected)">查询</li>
          </div>
          <div class="order-list">
            <div class="order-list-bg">
              <div v-if="order6List.length!=0" v-for="item in order7List">
                <div class="content-tab">
                  <li>
                    <span>订单编号：{{item.orderFlowNo}}</span>
                  </li>
                  <li>
                    <div class="left">
                      <img v-if='!!item.carProductPic':src="imgFileUrl+item.carProductPic" alt="">
                      <img v-if='item.carProductPic==null||item.carProductPic==undefined||item.carProductPic==""' :src="imgsrc" alt="">
                    </div>
                    <div class="right">
                      <p>
                        <span>
                          {{item.productNm}}&nbsp;
                          <span v-if="item.carProductKind=='2'?true:false">燃油</span>
                          <span v-if="item.carProductKind=='1'?true:false">电动</span>
                        </span>
                      </p>
                      <p>
                        <span v-if="item.carProductKind=='2'?true:false">燃油</span>
                        <span v-if="item.carProductKind=='1'?true:false">电动</span>
                        /{{item.carProductStyle}}/
                        {{item.productColor}}
                      </p>
                      <p>经销商：{{item.partnerNm}}</p>
                      <p><span>容量：{{item.carPowerBatkwh}}kWh</span><span>续航：{{item.carPowerMaxKm}}km</span></p>
                      <p><span>{{item.carProductKeep}}</span><span>{{item.financialServicesModel}}</span></p>
                      <p v-if="item.orderStatus=='8'"><span>履约保证金：<i>{{item.earnestMoney}}</i></span><span :style="(item.orderStatus=='1'||item.orderStatus=='3')?'color:#FF3F12':''">{{item.orderStatusStr}}</span></p>
                      <p v-if="item.orderStatus!='8'"><span>价格：<i>{{item.orderPrice}}万</i></span><span :style="(item.orderStatus=='1'||item.orderStatus=='3')?'color:#FF3F12':''">{{item.orderStatusStr}}</span></p>
                    </div>
                  </li>
                  <li>
                    <button class="btn" v-if="item.orderStatus==1?true:false">继续付款</button>
                    <button class="btn" @click="toApplyRefund(item.id)" v-if="item.orderStatus==2||item.orderStatus==6||item.orderStatus==8?true:false">申请退款</button>
                    <button class="btn" v-if="item.orderStatus==10?true:false" @click="friend">好友推荐</button>
                    <button class="btn" @click="orderinfo(item.id)">查看详情</button>
                  </li>
                </div>
              </div>
              <div v-if="order6List.length==0" class="nullTip">
                <img src="../../assets/image/tipNull.png">
                <p>暂无数据</p>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
      <van-dialog v-model="showFriend" show-cancel-button :before-close="beforeClose" confirm-button-text="推送" >
        <van-field v-model="username" label="姓名" placeholder="请输入姓名"  class="dialog-inp" />
        <van-field v-model="tel" type="phone" label="手机号" placeholder="请输入手机号" class="dialog-inp" />
      </van-dialog>
      <van-dialog v-model="withdrawtext" :before-close="closewithdraw" show-cancel-button>
        <van-cell-group  >
          <van-field
            v-model="withdrawtextt"
            type='textarea'
            placeholder="请输入撤销理由"
          />
      </van-cell-group>
      </van-dialog>
    </div>
  </div>
</template>

<script>
import imgsrc from '../../assets/image/defaltcar.png'
import * as homeApi from '../../api/home-api'
import { Dialog, Toast } from 'vant'
import { imgFileUrl } from '../../apiconfig/index'
import commonHeader from 'common/common-header'
export default {
  name: 'orderlist',
  components: {
    commonHeader
  },
  data() {
    return {
      active: this.$route.params.active || '0',
      routerLeft: '/day',
      tittle: '我的订单',
      selected: 'name1',
      pickerVisibleEnd: '',
      pickerVisibleBegin: '',
      isclickedEnd: false,
      isclickedBegin: false,
      yearBegin: '',
      monthBegin: '',
      dateBegin: '',
      yearEnd: '',
      monthEnd: '',
      dateEnd: '',
      imgFileUrl: imgFileUrl,
      showBegin: false,
      showEnd: false,
      currentDateBegin: new Date(),
      currentDateEnd: new Date(),
      minDateEnd: new Date(),
      imgsrc: imgsrc,
      orderStatusOption: [],
      order1List: [],
      order2List: [],
      order3List: [],
      order4List: [],
      order5List: [],
      order6List: [],
      order7List: [],
      page: {
        pageNumber: 1,
        pageSize: 1000000
      },
      search: {
        orderBeginTime: '',
        orderEndTime: ''
      },
      showFriend: false,
      username: '',
      tel: '',
      withdrawtext: false,
      withdrawtextt: '',
      revokeid: ''
    }
  },
  mounted() {
    this.selected = this.$route.params.tabName || 'name1'
    this.toSearch(this.selected)
    this.getTab1List()
    this.getTab2List()
    this.getTab3List()
    this.getTab4List()
    this.getTab5List()
    this.getTab6List()
    if (this.$route.params.active !== null && this.$route.params.active !== undefined) {
      this.getTab2List()
    } else if (this.$route.params.active === '5') {
      this.getTab6List()
    } else if(this.$route.params.active === '4'){
      this.getTab5List()
    }else if(this.$route.params.active === '3'){
      this.getTab4List()
    }else if(this.$route.params.active === '2'){
      this.getTab3List()
    }else if(this.$route.params.active === '1'){
      this.getTab2List()
    }else {
      this.getTab1List()
    }
  },
  created() {
    this.getTab1List()
    this.getLoadDataByType()
  },
  methods: {
    // 点击进入订单详情
    orderinfo(id){
      this.$router.push({path:'/orderinfo',name:'orderinfo',params:{orderid:id,checkcar:true}})
      console.log(this.$route.params.orderid,'id')
    },
    // 继续付款
    toPay(id, status) {
      if (status === 6 || status === 8) {
        let data = {
          'id': id.toString()
        }
        homeApi.orderPayOfBail(data).then(res => {
          if (res.data.code === '200') {
            Toast('支付成功！')
            this.getTab3List()
            this.active = '2'
          } else {
            Toast(res.data.msg)
          }
        })
      } else if (status === 7 || status === 11) {
        let data = {
          'id': id.toString()
        }
        homeApi.orderPayOfBalance(data).then(res => {
          if (res.data.code === '200') {
            Toast('支付成功！')
            this.getTab3List()
            this.active = '2'
          } else {
            Toast(res.data.msg)
          }
        })
      }
    },
    // 撤销申请
    withdraw(item) {
      this.withdrawtext = true
      this.revokeid = item.id
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } else if (type === 'month') {
        return `${value}月`
      } else if (type === 'day') {
        return `${value}日`
      }
      return value
    },
    popupBegin () {
      if (!this.showBegin) {
        this.currentDateBegin = new Date()
        this.showBegin = true
      } else {
        this.showBegin = false
      }
    },
    popupEnd() {
      if (this.yearBegin == undefined || this.yearBegin == null || this.yearBegin == '') {
        Dialog.alert({
          message: '请先选择开始时间'
        }).then(() => {})
        return
      }
      if (!this.showEnd) {
        this.currentDateEnd = this.minDateEnd
        this.showEnd = true
      } else {
        this.showEnd = false
      }
    },
    selectedBegin(value) {
      if (value == undefined && value == null && value == '') {
        value = new Date()
      }
      this.minDateEnd = value
      this.yearBegin = value.getFullYear()
      let month1 = value.getMonth() + 1
      this.monthBegin = '/' + (month1.toString().length == 1 ? '0' + month1 : month1)
      let date1 = value.getDate()
      this.dateBegin = '/' + (date1.toString().length == 1 ? '0' + date1 : date1)
      this.search.orderBeginTime = value
    },
    selectedEnd(value) {
      if (value == undefined && value == null && value == '') {
        value = this.minDateEnd
      }
      this.yearEnd = value.getFullYear()
      let month1 = value.getMonth() + 1
      this.monthEnd = '/' + (month1.toString().length == 1 ? '0' + month1 : month1)
      let date1 = value.getDate()
      this.dateEnd = '/' + (date1.toString().length == 1 ? '0' + date1 : date1)
      this.search.orderEndTime = value
    },
    getLoadDataByType() {
      let data = {
        type: 'ORDER_STATUS'
      }
      homeApi.loadDataByType(data).then(res => {
        if (res.data.code === '200') {
          this.orderStatusOption = res.data.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getTab1List() {
      let data = {
        orderBegintime: this.search.orderBeginTime,
        orderEndtime: this.search.orderEndTime,
        pageNumber: this.page.pageNumber,
        pageSize: this.page.pageSize
      }
      homeApi.orderList(data).then(res => {
        if (res.data.code === '200') {
          this.order1List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getTab2List() { // 未付款
      let data = {
        orderStatus: '6,7,8,11',
        orderBegintime: this.search.orderBeginTime,
        orderEndtime: this.search.orderEndTime,
        pageNumber: this.page.pageNumber,
        pageSize: this.page.pageSize
      }
      homeApi.orderList(data).then(res => {
        if (res.data.code === '200') {
          this.order2List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getTab3List() { // 备车中
      let data = {
        orderStatus: '2',
        orderBegintime: this.search.orderBeginTime,
        orderEndtime: this.search.orderEndTime,
        pageNumber: this.page.pageNumber,
        pageSize: this.page.pageSize
      }
      homeApi.orderList(data).then(res => {
        if (res.data.code == 200) {
          this.order3List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getTab4List() { // 线下验车
      let data = {
        orderStatus: '3,4',
        orderBegintime: this.search.orderBeginTime,
        orderEndtime: this.search.orderEndTime,
        pageNumber: this.page.pageNumber,
        pageSize: this.page.pageSize
      }
      homeApi.orderList(data).then(res => {
        if (res.data.code == 200) {
          this.order4List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getTab5List() { // 已取消
      let data = {
        orderStatus: '12',
        orderBegintime: this.search.orderBeginTime,
        orderEndtime: this.search.orderEndTime,
        pageNumber: this.page.pageNumber,
        pageSize: this.page.pageSize
      }
      homeApi.orderList(data).then(res => {
        if (res.data.code == 200) {
          this.order5List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getTab6List() { //申请退款
      let data = {
        orderStatus: '14,15',
        orderBegintime: this.search.orderBeginTime,
        orderEndtime: this.search.orderEndTime,
        pageNumber: this.page.pageNumber,
        pageSize: this.page.pageSize
      }
      homeApi.orderList(data).then(res => {
        if (res.data.code == 200) {
          this.order6List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getTab7List() { //已完成
      let data = {
        orderStatus: '10',
        orderBegintime: this.search.orderBeginTime,
        orderEndtime: this.search.orderEndTime,
        pageNumber: this.page.pageNumber,
        pageSize: this.page.pageSize
      }
      homeApi.orderList(data).then(res => {
        if (res.data.code == 200) {
          this.order7List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    toSearch(title) {
      if (title === '全部') {
        this.getTab1List()
      } else if (title === '待付款') {
        this.getTab2List()
      } else if (title === '备车中') {
        this.getTab3List()
      } else if (title === '线下验车') {
        this.getTab4List()
      } else if (title === '已取消') {
        this.getTab5List()
      } else if (title === '申请退款') {
        this.getTab6List()
      }else if (title === '已完成') {
        this.getTab7List()
      }
    },
    tabChange(index, title) {
      this.search.orderEndTime = ''
      this.search.orderBeginTime = ''
      this.currentDateBegin = ''
      this.currentDateEnd = ''
      this.yearBegin = ''
      this.monthBegin = ''
      this.dateBegin = ''
      this.yearEnd = ''
      this.monthEnd = ''
      this.dateEnd = ''

      if (title === '全部') {
        this.getTab1List()
      } else if (title === '待付款') {
        console.log('1111')
        this.getTab2List()
      } else if (title === '备车中') {
        this.getTab3List()
      } else if (title === '线下验车') {
        this.getTab4List()
      } else if (title === '已取消') {
        this.getTab5List()
      } else if (title === '申请退款') {
        this.getTab6List()
      }else if (title === '已完成') {
        this.getTab7List()
      }
    },
    friend() {
      this.showFriend = true
    },
    closewithdraw(action,done){
      if(action === 'confirm'){
        let params={
          id:this.revokeid
        }
        homeApi.revoke(params).then(res=>{
            console.log(res.data)
          if(res.data.code=='200'){
            Toast('撤销成功')
          }else{
            Toast(res.data.msg)
          }
        }).catch(err=>{
          console.log(err)
        })
      }
      done()
    },
    beforeClose(action, done) {
      if (action === 'confirm') {
        if (!this.username || !this.tel) {
          Toast('请输入姓名或手机号')
          done(false)
          return
        }
        if (!(/^1[34578]\d{9}$/.test(this.tel))) {
          Toast('手机号格式错误')
          done(false)
          return
        }
        let data = {
          ofuser: this.tel,
          ofuserName: this.username,
          carproductcd: this.form.carProductCd,
          productColor: this.form.productColor,
          financeId: this.form.financialServicesModel,
          partnerId: this.form.partnerId,
          paymentWay: this.form.orderPay,
          earnestMoney: this.form.earnestMoney
        }
        console.log('data==' + JSON.stringify(data))
        homeApi.addcar(data).then(res => {
          if (res.data.code==200) {
            done()
            Toast('好友推荐成功')
          } else {
            done()
            Toast(res.data.msg)
          }
        }).catch(err => {
          console.log(err)
        })
        // setTimeout(done, 1000)
      } else if (action === 'cancel') {
        this.username = ''
        this.tel = ''
        done() //关闭
      }
    },
    toApplyRefund(id) {
      this.$router.push({path:'/drawbackinfo',name:'drawbackinfo',params:{orderid:id,checkcar:true}})
    },
    toConfirm() {
    }
  }
}
</script>

