      height: 2.8rem;
      align-items: center;
      padding:1.3rem 0.4rem 0 0.4rem;
      li:nth-child(1){
        .pr(10);
      }
      li:nth-child(2){
        width: 20%;
        width: 3rem;
        height: 0.7rem;
        border: 1px solid #D7D7D7;
        border-radius:4px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .padding(0,10);
        .text-time{
          .mt(4);
        }
        img{
          width: 0.45rem;
          height: 0.45rem;
          margin-right: 0.05rem;
        }
      }
      li:nth-child(3){
        .margin(0,10);
        width: 0.25rem;
        border: 0.5px solid #D7D7D7;
      }
      li:nth-child(4){
        width: 20%;
        width: 3rem;
        height: 0.7rem;
        border: 1px solid #D7D7D7;
        border-radius: 4px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .padding(0,10);
        .text-time{
          .mt(4);
        }
        img{
          width: 0.45rem;
          height: 0.45rem;
          margin-right: 0.05rem;
        }
      }
      li:nth-child(5){
        .padding(0,20);
        color: #F35818;
      }

    }
    .nullTip{background-color:#fff;height:70vh;padding-top:20vh;
      img{
        width:50%;height:auto;
      }
      p{
        color:#999999;font-size:0.4rem;
      }
    }
    .content-tab{
      border-radius:5px;
      background: #fff;
      padding: 0 0.4rem;
      .mb(20);
      li:nth-child(1){
        width: 100%;
        display: flex;
        justify-content: space-between;
        background: #fff;
        .fs(30);
        padding-top: 0.4rem;
        span:nth-child(1){
          color:#9E9E9E ;
        }
        span:nth-child(2){
          color:#2ED36B ;
        }
      }
      li:nth-child(2){
        color: #989898;
        height: auto;
        display: flex;
        justify-content: space-between;
        background: #fff;
        align-items: center;
        margin-top: 0.3rem;
        height: 3.6rem;
        border-bottom: 1px solid #F5F5F5;
        padding-bottom: 0.2rem;
        .left{
          width: 30%;
          height: 100%;
          img{
            width: 100%;
            height: 100%;
            border-radius: 0.2rem;
            vertical-align: middle;
          }
        }
        .right{
          width: 70%;
          text-align: left;
          padding-left: 0.3rem;
          line-height: 0.6rem;
          .fs(30);
          p{.fs(28);}
          p:nth-child(1){
            color: #000;
            .fs(33);
          }
          p:nth-child(4){
            display: flex;
            justify-content: space-between;
          }
          p:nth-child(5){
            display: flex;
            justify-content: space-between;
          }
          p:nth-child(6){
            display: flex;
            justify-content: space-between;
            span:nth-child(1){
              i{
                color: #FF3F12;
                font-weight: 600;
                font-style:normal
              }
            }
            span:nth-child(2){
              color: #2ED36B;
            }
          }
        }
      }
      li:nth-child(3){
        margin-top: 0.3rem;
        display: flex;
        justify-content: flex-end;
        .pb(30);
        .btn{
          .fs(28);
          background: #fff;
          border: 1px solid #F68151;
          border-radius: 4px;
          .padding(10,24);
        }
        .btn:nth-child(1){
          background: #F45416;
          color: #fff;
        }
        .btn:nth-child(2){
          color: #F45416;
          margin-left: 0.3rem;
          margin-right: 0.3rem;
        }
        .btn:nth-child(3){
          color: #F45416;
        }
      }
    }
    .van-dialog{
      padding-top:0.4rem;
      .fs(30);
      .dialog-inp{
        height: 1rem!important;
        line-height: 1rem;
        text-align: left;
        padding-left: 0.3rem;
      }
    }
  }
</style>



